export default {
  name: 'NewsNav',
  data: function data() {
    return {
      // navList: [
      //   { name: "公司动态", link: "/News", activeIndex: 0 },
      //   { name: "恒大研究院", link: "/News/News_Research", activeIndex: 1 },
      //   { name: "平台公告", link: "/News/News_Notice", activeIndex: 2 },
      //   { name: "投资学堂", link: "/News/News_School", activeIndex: 3 }
      // ],
      navList: [{
        name: "公司动态",
        link: "/News",
        activeIndex: 0
      }, {
        name: "恒大研究院",
        link: "/News/News_Research",
