        name: "投资学堂",
        link: "/News/News_School",
        activeIndex: 2
      }],
      selectIndex: 0
    };
  },
  watch: {
    '$route': 'changePath'
  },
  methods: {
    changeNav: function changeNav(item, index) {
      var currentRoute = this.$route.path;

      if (currentRoute == item.link) {
        //点击同一个路由直接return
        return;
      }

      this.selectIndex = index;
      this.$router.push({
        path: item.link
      });
    },
    innitIndex: function innitIndex() {
      var _this = this;

      var currentRoute = this.$route.path;
      console.log(1);
      console.log(currentRoute);
      this.navList.forEach(function (item, i) {
        if (currentRoute.indexOf(item.link) > -1) {
          _this.selectIndex = item.activeIndex;
        }
      });
    },
    changePath: function changePath() {
      this.innitIndex();
    }
  },
  mounted: function mounted() {
    this.innitIndex();
  }
};